import React, { useEffect, useState } from "react";
import axios from "axios";
import { port } from "../../assets/collection";
import Lodr from "../section/lodr";
import {
  CFormLabel,
  CInputGroup,
  CInputGroupText,
  CFormInput,
  CButton,
  CForm,

} from "@coreui/react";
import CIcon from "@coreui/icons-react";
import { cilCheck, cilX, cilPen } from "@coreui/icons";

export default function adspage() {
  const [ads, setads] = useState(null);
  const [admain, setadmain] = useState({client:'',slot:''})
  const [adleft, setadleft] = useState([{client:'',slot:''}])
  const [adright, setadright] = useState([{client:'',slot:''}])
  const [progress, setprogress] = useState(false);

  useEffect(() => {
    axios.get(port + "api/getAds").then((res) => {
      setprogress(true);
      setads(res.data[0]);
    });
  }, []);
  useEffect(() => {
    if (ads) {
      ads.admain?setadmain(ads.admain):null
      ads.adleft?setadleft(ads.adleft):null
      ads.adright?setadright(ads.adright):null
    }
  }, [ads])

  const submitHandler = () => {
    setprogress(false);
    axios
      .post(port + "api/admin/addAds", {
        admain: admain,adleft:adleft,adright:adright
      })
      .then(() => {
        alert("ads added successfully");
        window.location.reload(false);
      });
  };

  return progress ? (
    <div className="container-fluid">
      <p>
        Add "data-ad-client" and "data-ad-slot" values on the given fields
        respectively.
      </p>
      <div className="row">
        <CFormLabel style={{fontWeight:'bolder',textAlign:'center'}} htmlFor="ethnic" className="col-12 col-form-label">
          Ads Main Horizontal
        </CFormLabel>
        <CForm className="col-6">
          <CFormInput
            type="text"
            id="exampleFormControlInput1"
            label="Client"
            value={admain.client}
            onChange={(e)=>setadmain({client:e.target.value,slot:admain.slot})}
            aria-describedby="exampleFormControlInputHelpInline"
          />
        </CForm>
        <CForm className="col-6">
          <CFormInput
            type="text"
            id="exampleFormControlInput1"
            label="slot"
            value={admain.slot}
            onChange={(e)=>setadmain({client:admain.client,slot:e.target.value})}
            aria-describedby="exampleFormControlInputHelpInline"
          />
        </CForm>
      </div>
      <br></br>
      <div className="row">
        <CFormLabel style={{fontWeight:'bolder',textAlign:'center'}} htmlFor="ethnic" className="col-12 col-form-label">
          Ads Left Vertical
        </CFormLabel>
        <CForm className="col-6">
          <CFormInput
            type="text"
            id="exampleFormControlInput1"
            label="Client"
            value={adleft.client}
            onChange={(e)=>setadleft({client:e.target.value,slot:adleft.slot})}
            aria-describedby="exampleFormControlInputHelpInline"
          />
        </CForm>
        <CForm className="col-6">
          <CFormInput
            type="text"
            id="exampleFormControlInput1"
            label="slot"
            value={adleft.slot}
            onChange={(e)=>setadleft({client:adleft.client,slot:e.target.value})}
            aria-describedby="exampleFormControlInputHelpInline"
          />
        </CForm>
      </div>
      <br></br>
      <div className="row">
        <CFormLabel style={{fontWeight:'bolder',textAlign:'center'}} htmlFor="ethnic" className="col-12 col-form-label">
          Ads Right Vertical
        </CFormLabel>
        <CForm className="col-6">
          <CFormInput
            type="text"
            id="exampleFormControlInput1"
            label="Client"
            value={adright.client}
            onChange={(e)=>setadright({client:e.target.value,slot:adright.slot})}
            aria-describedby="exampleFormControlInputHelpInline"
          />
        </CForm>
        <CForm className="col-6">
          <CFormInput
            type="text"
            id="exampleFormControlInput1"
            label="slot"
            value={adright.slot}
            onChange={(e)=>setadright({client:adright.client,slot:e.target.value})}
            aria-describedby="exampleFormControlInputHelpInline"
          />
        </CForm>
      </div>
      <br></br>
      <CButton onClick={submitHandler} color="success">
        Upload
      </CButton>
      <br/>
    </div>
  ) : (
    <Lodr />
  );
}
